.more{
    height: 100vh;
}
.info{
    text-align: center;
    font-size: 40px;
    margin-bottom: 30px;   
}
.m{
    display: flex;
    
}
.contact{
    flex: 1;
    position: relative;
}
.resume{
    flex: 1;
}
.a-bg{
    width: 20px;
    height: 100%;
    position: absolute;
}
.a-wrapper{
    padding: 50px;
    display: flex;
    
}
.a-contact{
    font-size: 30px;
    text-align: center;
    padding: 20px;
    margin-top: 20px;
    padding-left: 100px;
    
}
 .a-contacts{
    padding-left: 25px;
    
}
.a-contact-list{
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 500;
    width: 70%;
}
.a-icon{
    width: 30px;
    height: 30px;
    margin-right: 20px;
}
.myresume{
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn{
    padding-top: 20px;
}
.resume-info{
    margin-top: 60px;
}
.a-resume img{
    padding-left: 200px;
    padding-top: 60px;
    width: 50vw;
    height: 60bh;
}

ul{
    background: linear-gradient(68.6deg, rgb(252, 165, 241) 1.8%, rgb(181, 255, 255) 100.5%);
    height: 68vh;
    width: 40vw;
    border: 0;
    border-radius: 20px;
}
.resume h2{
    font-size: 27px;
    text-align: center;
    font-weight: bold;
    margin-top: 80px;
}