@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap');
.a {
    height: 100vh;
    display: flex;
    align-items: center;
  }
  
  .a-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
  }
  
  .a-right {
    flex: 1;
    position: relative;
  }


  .a-card.bg {
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;
  }
  
  .a-card {
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
  }
  
  .a-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  img.mern{
    width: 350px;
    height: 300px;
    position: absolute;
    padding-left: 40%;
    padding-bottom: 150px;
  }
  .a-title{
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
  }
  ul{
    padding-top: 20px;
  }
  ul li{
    padding: 5px 10px;
  }
  h3{
    margin: 9px;
    font-size: 18px;
    font-weight: 600;
  }